@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
*,
*::before,
*::after {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body {
   background: #28292c;
}

html,
body {
   height: 100%;
}

/* fonts */

.App__banner{text-align:center}.App__title{color:#fff;font-family:'Bebas Neue', cursive;font-size:60px;margin:5px 15px;line-height:60px;font-weight:normal}.App__line{background:#fff;height:1px}

.NoteCreator{border:1px #9aa0a6 solid;border-radius:1vw;width:500px;margin:30px auto;padding:5px;color:#fff}.NoteCreator__titleContainer{margin-bottom:10px;font-size:20px;position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.NoteCreator__pin{float:right;font-size:30px;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0);padding:0px 5px}.NoteCreator__pin:hover{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}.NoteCreator__pin--pined{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}.NoteCreator__pin--pined:hover{-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0)}.NoteCreator__textConteiner{max-height:30vh;overflow-y:auto}.NoteCreator__titleValue{outline:none;width:-webkit-max-content;width:max-content;width:100%;word-break:break-all;font-size:30px}.NoteCreator__contentContainer{font-size:20px;position:relative}.NoteCreator__contentValue{outline:none;width:-webkit-max-content;width:max-content;width:100%;word-break:break-all;font-size:20px}.NoteCreator__textHolder{color:#bdc3c7;pointer-events:none;font-size:20px;position:absolute;top:0;left:0}.NoteCreator__textHolder--title{font-size:30px}.NoteCreator__options{margin:10px 0px 2px 5px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-size:20px}.NoteCreator__addBtn,.NoteCreator__closeBtn{margin:0px 5px;cursor:pointer}

.Palette{position:relative}.Palette:hover .Palette__colorContainer{display:grid}.Palette__colorContainer{background:#202124;position:absolute;bottom:100%;left:0;padding:5px;display:none;grid-template-columns:26px 26px 26px 26px;grid-template-rows:26px 26px 26px;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px;grid-row-gap:10px;row-gap:10px;box-shadow:-4px 5px 18px 0px rgba(50,50,50,0.85)}.Palette__color{border-radius:50%}.Palette__color:hover{border:2px solid #fff}.Palette__color--current{border:2px solid #fff}

@-webkit-keyframes fadeOut{0%{opacity:1;max-height:200px;width:300px}50%{opacity:0;max-height:200px;width:300px}100%{opacity:0;height:0px;width:0}}@keyframes fadeOut{0%{opacity:1;max-height:200px;width:300px}50%{opacity:0;max-height:200px;width:300px}100%{opacity:0;height:0px;width:0}}@-webkit-keyframes fadeIn{0%{opacity:0;height:0px;width:0}40%{opacity:0;height:0px;width:300px}100%{opacity:1;max-height:200px;width:300px}}@keyframes fadeIn{0%{opacity:0;height:0px;width:0}40%{opacity:0;height:0px;width:300px}100%{opacity:1;max-height:200px;width:300px}}.noteFadeOut{-webkit-animation:fadeOut 0.5s ease-in-out;animation:fadeOut 0.5s ease-in-out}.noteFadeIn{-webkit-animation:fadeIn 0.5s ease-in-out;animation:fadeIn 0.5s ease-in-out;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.Note{display:-webkit-flex;display:flex;display:flex;-webkit-flex-direction:column;flex-direction:column;border:2px solid #9aa0a6;border-radius:15px;opacity:0;color:white;padding:8px;margin:8px;-webkit-justify-content:space-between;justify-content:space-between}.Note:hover .Note__pinIcon,.Note:hover .Note__options i{opacity:1}.Note__textContainer{cursor:pointer;overflow-y:hidden}.Note__title{padding:2px}.Note__titleValue{width:100%;font-size:25px;background:none;border:none;word-wrap:break-word}.Note__pinIcon{opacity:0;transition:opacity 0.3s ease;float:right;font-size:25px;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0);padding:0px 5px}.Note__pinIcon:hover{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}.Note__pinIcon--pined{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}.Note__pinIcon--pined:hover{-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0)}.Note__content{font-size:20px;word-wrap:break-word}.Note__options i{opacity:0;text-align:center;font-size:20px;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0);transition:opacity 0.3s ease;padding-top:8px;padding-right:12px}.Note__options i:hover{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}

.curtain{z-index:999;position:fixed;top:0;left:0;width:100vw;height:100vh;background:RGBA(32, 33, 36, 0.9);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.NoteEditor{color:white;border:1px #9aa0a6 solid;border-radius:1vw;width:550px;margin:0 auto;padding:10px;color:#fff}.NoteEditor__titleContainer{font-size:30px;position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.NoteEditor__pin{float:right;font-size:30px;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0);padding:0px 5px}.NoteEditor__pin:hover{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}.NoteEditor__pin--pined{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}.NoteEditor__pin--pined:hover{-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0)}.NoteEditor__textContainer{max-height:50vh;overflow-y:auto}.NoteEditor__titleValue{outline:none;width:100%;word-break:break-all}.NoteEditor__contentContainer{margin:10px 0px;font-size:20px;position:relative}.NoteEditor__contentValue{outline:none;width:100%;word-break:break-all}.NoteEditor__textHolder{color:#bdc3c7;pointer-events:none;font-size:20px;position:absolute;top:0;left:0}.NoteEditor__textHolder--title{font-size:30px}.NoteEditor__options{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-size:25px}.NoteEditor__option,.NoteEditor .Palette{padding:0px 6px;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#bdc3c7;color:rgba(0,0,0,0)}.NoteEditor__option:hover,.NoteEditor .Palette:hover{-webkit-text-stroke-width:0px;-webkit-text-stroke-color:#bdc3c7;color:#fff}.NoteEditor__closeBtn{margin:0px 5px;cursor:pointer}

.NoteBook__main{width:80vw;margin:0 auto}.NoteBook__category{color:#9aa0a6}.NoteBook__noteDisplay{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}

